import React, { Component } from 'react';
import { string, shape } from 'prop-types';

import { Logo } from '../../../components/Logo';
import { SignupForm } from '../../../components/SignupForm';
import { TermsLink } from '../../../components/TermsLink';

import './MainBlock.scss';

export default class MainBlock extends Component{
  constructor(props) {
    super(props);

    this.state = {
      mode: 'working'
    };
  }

  render(){
    const { params } = this.props;
    const { mode } = this.state;

    return (
      <div className='main-block__container'>
        <div>
          <div className='icon__container'>
            <div className='logo animated'>
              {params && params.previewMode ? null : (
                <Logo
                  id='logo-top'
                  className='sh-logo'
                  onLogoReady={this.startChat}
                  mode={mode}
                />
              )}
            </div>

            <div className='copytext message__container'>{params.message}</div>
          </div>

          <div className='animation__wrapper'>
            <div className='animation__moved animation__wrapper'>
              <h1
                className='small'
                dangerouslySetInnerHTML={{ __html: params.title }}
              />

              <div className='animation__moved animation__wrapper'>
                <p
                  className='main-block__description copytext'
                  dangerouslySetInnerHTML={{ __html: params.description }}
                />

                <div className='animation__appeared'>
                  <SignupForm
                    hasAutofocus={false}
                    buttonText={params.buttonText}
                    inputPlaceholder={params.inputPlaceholder}
                  />

                  {params && params.previewMode
                    ? null
                    : params && <TermsLink />}
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

MainBlock.propTypes = {
  params: shape({
    title: string,
    description: string,
    inputPlaceholder: string,
    buttonText: string
  })
};



