import React from 'react';
import { arrayOf, string, shape } from 'prop-types';

import './Steps.scss';

const Step = ({ step }) => (
  <div className='step__container'>
    <h4 className='step__index'>{step.index}</h4>

    <div className='copytext step__text'>{step.text}</div>
  </div>
);

const Steps = ({ params }) => (
  <div className='steps__container'>
    <div className='steps__description'>
      <h2>{params.title}</h2>

      <p className='copytext-large'>{params.description}</p>
    </div>

    <div className='steps__content'>
      <div className='steps__list'>
        <img src={`${process.env.ASSETS_URL}backgrounds/steps.png`} />

        {params.items.map((item, index) => <Step step={item} key={index} />)}
      </div>

      <h3 className='steps__success'>{params.successText}</h3>
    </div>
  </div>
);

Steps.propTypes = {
  title: string,
  description: string,
  successText: string,
  items: arrayOf(
    shape({
      title: string,
      icon: string,
      color: string,
      text: string
    })
  )
};

Steps.defaultProps = {
  items: [],
  title: '',
  description: '',
  successText: ''
};

export default Steps;



