import React from 'react';

import { Features } from './Features';
import { MainBlock } from './MainBlock';
import { ExtraBlock } from '../../components/ExtraBlock';
import { Steps } from './Steps';
import { TrustBlock } from '../../components/TrustBlock';
import { Divider } from '../../ui';

export default ({ data }) => {
  const page = data.markdownRemark;
  const { features, mainBlock, extraBlock, steps } = page.frontmatter;

  return (
    <div>
      <MainBlock params={mainBlock} />

      <Divider fullWidth />

      <TrustBlock />

      <Features params={features} />

      <Divider />

      <Steps params={steps} />

      <ExtraBlock params={extraBlock} />

      <div dangerouslySetInnerHTML={{ __html: page.html }} />
    </div>
  );
};

export const query = graphql`
  query HomePageQuery {
    markdownRemark(fields: { slug: { eq: "/" } }) {
      html
      frontmatter {
        title
        mainBlock {
          title
          description
          buttonText
          message
          inputPlaceholder
        }
        extraBlock {
          title
          description
          buttonText
          inputPlaceholder
        }
        features {
          title
          bgImage
          items {
            title
            icon
            description
            color
          }
        }
        steps {
          title
          successText
          description
          items {
            index
            text
          }
        }
      }
    }
  }
`;



