import React, { Component } from 'react';
import { string } from 'prop-types';

import './Logo.scss';

export default class Logo extends Component {
  constructor(props){
    super(props);

    this.logo = null;
    this.state = {
      mode: props.mode
    };
    this.loadLogo = this.loadLogo.bind(this);
  }

  componentWillReceiveProps(newProps){
    if (newProps.mode !== this.state.mode){
      this.setState(
        {
          mode: newProps.mode
        },
        () => {
          try {
            this.loadAnimation();
          } catch (exp){
            console.log(exp);
          }
        }
      );
    }
  }

  componentDidMount(){
    window.addEventListener('load', this.loadLogo);

    if (window.SHLogo){
      this.loadLogo();
    }
  }

  componentWillUnmount(){
    window.removeEventListener('load', this.loadLogo);
    window.PIXI.loader.resources = {};
  }

  loadLogo(){
    // VZ: Check why SHLogo is not loaded adter 'load' event
    if (!window.SHLogo){
      setTimeout(this.loadLogo, 1000);
      return;
    }
    this.logo = new window.SHLogo.Logo(this.getId(), () => {
      this.loadAnimation();
    });
  }

  getId(){
    const { id } = this.props;
    return `${id}-sh-logo`;
  }

  loadAnimation(){
    const { mode } = this.state;

    switch (mode){
      case 'relaxing':
        return this.logo.relaxing();
      case 'waiting':
        return this.logo.waiting();
      case 'working':
      default:
        return this.logo.working();
    }
  }

  render(){
    const { className } = this.props;

    return <div id={this.getId()} className={className} />;
  }
}

Logo.propTypes = {
  className: string,
  mode: string,
  id: string
};



